<template>
  <div id="app">
    <toolbar/>
    <hello></hello>
    <foot></foot>
  </div>
</template>

<script>
  import 'normalize.css'
  import Hello from './components/Content'
  import Toolbar from './components/Toolbar'
  import Foot from './components/Foot'
  import $ from 'jquery'

  export default {
    name: 'app',
    components: {
      Hello, Toolbar, Foot
    },
    mounted () {
      var height = $('#toolbar').height() + 20
      console.log(height)
      $('#content').css('marginTop', height)
    }
  }
</script>

<style>

  #app {
    font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 500;
    text-align: center;
  }

</style>



