<template>
  <div id="toolbar">
    <a id="logo" href="/">
      <img src="../assets/logo.png"/>
      <span>{{title}}</span>
    </a>

    <ul id="nav">
      <li>
        <a href="/">文章</a>
      </li>
      <li>
        <a href="/">作品</a>
      </li>
      <li>
        <a href="/">关于</a>
      </li>
    </ul>
  </div>
</template>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  #toolbar
    min-height 50px
    position fixed
    width 100%
    top 0
    box-shadow 0 0 1px rgba(0, 0, 0, 0.25)
    background-color #fff

  a
    text-decoration none
    color black

  #logo
    display inline
    line-height 40px
    float left
    color #2c3e50
    img
      vertical-align middle
      margin-left 40px
      height 40px
      width 40px
      border none
      transition width 0.3s, height 0.3s
    img:hover
      width 60px
      height 60px

  #nav
    display inline-block
    float right
    margin-right 50px
    li
      display inline-block
      min-width 100px
      a
        transition color 0.3s
    li:hover
      a
        color #42b983
</style>

<script>
  export default{
    data () {
      return {
        title: 'J1aDong\'s site'
      }
    }
  }
</script>



